$size: 1.5vw;
$depth: $size / 2 * -1;

// TimeUnit colors
$main-color: rgb(58, 233, 58);
$outdated-color: #bbb;

$work: maroon;
$rest: yellowgreen;
$gym: teal;
$sleep: mediumturquoise;

$front-color: $main-color;
$back-color: darken($main-color, 15%);
$left-color: darken($main-color, 10%);
$right-color: darken($main-color, 10%);
$top-color: darken($main-color, 5%);
$bottom-color: darken($main-color, 15%);

// Mobile
$mobile: 700px;
$size-m: 4vw;
$depth-m: $size-m / 2 * -1;